@import '../Assets/Styles/variables';

@media screen {
    .container {
        display: flex;
        flex-direction: column;
        background-color: $light-grey;
        width: $max-width * 0.4;
        height: $max-height * 0.6;
        border-radius: 50px;
        overflow: hidden;
        // &-message {
        //     // height: 100%;
        //     display: flex;
        //     align-items: center;
        //     justify-content: center;
        //     font-size: $p50;
        //     font-weight: 700;
        //     color: $danger;
        // }
    }

    @media screen and (max-width: 1200px) {
        .container {
            width: $max-width * 0.4;
            height: $max-height * 0.6;
            // &-message {
            //     // height: 100%;
            //     display: flex;
            //     align-items: center;
            //     justify-content: center;
            //     font-size: $p50;
            //     font-weight: 700;
            // }
        }
    }

    @media screen and (max-width: 992px) {
        .container {
            width: $max-width * 0.5;
            // height: $max-height * 0.8;
            transition: all 0.3s;
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -ms-transition: all 0.3s;
            -o-transition: all 0.3s;
            // &-message {
