            //     align-items: center;
            //     justify-content: center;
            //     font-size: $p50;
            //     font-weight: 700;
            // }
        }
    }

    @media screen and (max-width: 768px) {
        .container {
            width: $max-width * 0.9;
            height: $max-height * 0.8;
            transition: all 0.3s;
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -ms-transition: all 0.3s;
            -o-transition: all 0.3s;
            // &-message {
            //     // height: 100%;
            //     display: flex;
            //     align-items: center;
            //     justify-content: center;
